.click-item {
  height: 15rem;
  width: 15rem;
  margin: 3rem;
  position: relative;
}

.imgNumber {
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: 1;
}

.playerImage {
  height: 100%;
  position: absolute;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  border-bottom: .2rem solid #000;
}

.playerImage:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0px 15px 10px -15px #111;
          box-shadow: 0px 15px 10px -15px #111;
}



